.backdrop {
    display: none;
    left: 0;
    top: 0;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }
  .mobileMenu {
   position: fixed;
    z-index: 2;
    padding: 18px 20px;
    top: 0;
    left: 0;
    background-color: var(--button-color);
    width: 100%;
    height: 100vh;
    overflow: auto;
  }
  .userBarBtnWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .closeBtn {
    background-color: transparent;
  }
  .closeBtnIcon {
    width: 20px;
    height: 20px;
    fill: var(--background-color);
  }
  .noScroll {
    overflow: hidden;
  }
  
  @media screen and (min-width: 768px) {
    .mobileMenu {
      /* width: 385px; */
  
      /* responsive */
      width: 50vw;
      height: 100vh;
      right: 0;
      left: auto;
    }
    .backdrop {
      display: block;
    }
  }
  
  @media screen and (min-width: 1440px) {
    .mobileMenu {
      display: none;
    }
    .backdrop {
      display: none;
    }
  }